.layout_container{
    margin-top: 70px
}

.border_div{
    display: flex;
    flex-direction: column;
    gap: 0.7rem;
    border-radius: 2rem;
    border: 1px solid rgb(43, 149, 219);
    padding: 1.4rem;
    letter-spacing: 0.1rem;
    font-size: 1.3rem;
    color: rgb(200, 200, 200);
    background-color: rgb(54, 57, 63);
    box-shadow: rgb(0 233 206 / 33%) 0px 0px 20px, rgb(24 25 28) 0px 0px 10px inset;
}
.button-div button{
    text-transform: uppercase;
    background-color: rgb(43, 149, 219);
    background-image: linear-gradient(125deg, rgb(43, 149, 219) 0%, rgb(130, 71, 176) 50%, rgb(43, 149, 219) 100%);
    color: rgb(255, 255, 255);
    border-radius: 1rem;
    padding: 1rem 2rem;
    width: 100%;
    text-align: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    transition: all 200ms ease 0s;
    font-size: 1.3rem;
    border: none;
}
.button-div button:hover{
    transform: translateY(-5px);
    -webkit-box-shadow: 0px 10px 20px -9px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 10px 20px -9px rgba(0,0,0,0.75);
    box-shadow: 0px 10px 20px -9px rgba(0,0,0,0.75);
}
.button-div{
    display: flex;
    height: 100%;
    -webkit-box-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    align-items: center;
}
.layout_parent{
    margin: 0 auto;
display: grid;
width: 70%;
grid-template-columns: 1fr 1fr;
grid-template-rows: 1fr 1fr;

  gap: 1em;
}
.stats-div_1{
    display: flex;
    align-items: center;
    justify-content: center;
}
.stats-div_1 p{
    color: rgb(43, 149, 219);
    font-size: 4rem;
    text-shadow: rgb(24 25 28) 0px 0px 10px;
    margin: 0
}
.stats-div_1 span{
    color: rgb(43, 149, 219);
    font-size: 2rem;
}
.daily-return{
    color: rgb(43, 149, 219);
}
.apr_container, .fee_container{
    line-height: 1.3rem;
    padding: 0.1rem 0.4rem;
    display: flex;
    flex-direction: row;
    -webkit-box-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    align-items: center;
}
.apr_container{
    margin-top: 1em
}
.input-container{
    display: flex;
    -webkit-box-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    align-items: center;
    border: 1px solid rgb(43, 149, 219);
    border-radius: 1rem;
    padding: 0.5rem 2rem;
    background-color: rgb(255, 255, 255);
    width: 100%;
}

.input-container input{
    -webkit-box-flex: 1;
    flex-grow: 1;
    flex-shrink: 1;
    font-size: 1.3rem;
    margin: 0.5rem;
    width: 10ch;
    color: rgb(24, 25, 28);
    
    border: none
}
.button{
    text-transform: uppercase;
    background-color: rgb(34, 118, 173);
    background-image: unset;
    color: rgb(255, 255, 255);
    border-radius: 1rem;
    padding: .5rem 1rem;
    width: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    transition: all 200ms ease 0s;
    font-size: 1em;
    border: none
}
h1{
    color: rgb(255, 255, 255);
    font-size: 1.5em;
    text-transform: uppercase;
    text-shadow: rgb(0 233 206) 0px 0px 4px;
    text-align: left;
    margin: 0 
}
.parrafo{
    font-size: 0.8rem;
    text-align: left;
    margin: 0;
}
.connect_div{
    grid-row: span 2;
}

@media (max-width: 840px){
    .layout_parent{
        -webkit-box-flex: 1;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    -webkit-box-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    align-items: center;
    }
    .connect_div, .stats_div, .refferral_div{
        width: 100%
    }
}
.refferral_div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}
.span-cro{
    font-size: .7rem;
    font-weight: 600;
    transform: translateY(-10px);
}
.absolve-div{
    display: flex;
   
    -webkit-box-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    align-items: center;
}
.absolve-div button{
    text-transform: uppercase;
    background-color: rgb(43, 149, 219);
    background-image: linear-gradient(125deg, rgb(43, 149, 219) 0%, rgb(130, 71, 176) 50%, rgb(43, 149, 219) 100%);
    color: rgb(240, 240, 240);
    border-radius: 1rem;
    padding: 1rem 2rem;
    width: 100%;
    text-align: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    transition: all 200ms ease 0s;
    font-size: 1.3rem;
    border: none;
}
.absolve-div button:hover{
    transform: translateY(-5px);
    -webkit-box-shadow: 0px 10px 20px -9px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 10px 20px -9px rgba(0,0,0,0.75);
    box-shadow: 0px 10px 20px -9px rgba(0,0,0,0.75);
}
.cro-p{
    font-size: 1.4rem;
    color: #000;
    margin: 0;

}
.hr{
    width:100%;
    height: 4px;
    border: none;
    background: rgb(43, 149, 219);;
}
.reward-container{
    display: flex;
    
    justify-content: space-between;
    display: flex;
    -webkit-box-pack: center;
   
    -webkit-box-align: center;
    align-items: center;
    border: 1px solid rgb(43, 149, 219);
    border-radius: 1rem;
    padding: 0 2rem;
    background-color: rgba(0,0,0,0.5);
    width: 100%;
    -webkit-box-shadow: inset 0px 0px 12px 0px rgba(43, 149, 219);
    -moz-box-shadow: inset 0px 0px 12px 0px rgba(43, 149, 219);
    box-shadow: inset 0px 0px 12px 0px rgba(43, 149, 219);
}

.reward-container p.reward {
    margin-top: 15px;
}
.amount-cro{
    display: flex;
    align-items: center;
}
.amount-cro p{
    font-size: 1.6rem;
    font-weight: 600;
}

.amount-cro p.reward-amount {
    margin-top: 15px;
}

.span-cro{
    margin-top: 25px;
    font-size: .8rem;
}
.button_parent{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
}
.button-absolve{
    text-transform: uppercase;
    background-color: rgb(84, 135, 211);
   
    color: rgb(240, 240, 240);
    border-radius: 1rem;
    padding: 0 1rem;
    width: 100%;
    align-items: center;
    display: flex;
    flex-direction: column;
    transition: all 200ms ease 0s;
    font-size: 1em;
    border: none
}
.button-absolve p{
    margin-top: 10px;
    margin-bottom: 0;
}

.button-absolve span{
    margin-bottom: 20px;
    font-size: .8rem;
}
.button-absolve:hover{
    transform: translateY(-5px);
    -webkit-box-shadow: 0px 10px 20px -9px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 10px 20px -9px rgba(0,0,0,0.75);
    box-shadow: 0px 10px 20px -9px rgba(0,0,0,0.75);
}
.button-container{
    
    flex-grow: 1;
}