#root{
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(24, 25, 28);
   
  }
  body{
    padding: 0;
    margin: 0;
   box-sizing: border-box;
   font-family: Rubik, Helvetica, Arial, sans-serif;
  }
  
  img {
    max-width: 100%;
  }
.logo_container{

    position: relative;
    width: 500px;
    height: 300px;
    margin: 20px auto;
}
.frame-color{
        
    position: absolute;
    top: 0px;
    left: 0px;
    width: 400px;
    height: 400px;

}
.text-wizzard{
    position: absolute;
    top: 50px;
    left: 0px;
    width: 150%;
    height: 90%;  
}
.wizzard{
    position: absolute;
    top: -100px;
    left: 0px;
    width: 400px;
    height: 400px;   
}
.layout_container{
    margin-top: 70px
}

.border_div{
    display: flex;
    flex-direction: column;
    grid-gap: 0.7rem;
    gap: 0.7rem;
    border-radius: 2rem;
    border: 1px solid rgb(43, 149, 219);
    padding: 1.4rem;
    letter-spacing: 0.1rem;
    font-size: 1.3rem;
    color: rgb(200, 200, 200);
    background-color: rgb(54, 57, 63);
    box-shadow: rgb(0 233 206 / 33%) 0px 0px 20px, rgb(24 25 28) 0px 0px 10px inset;
}
.button-div button{
    text-transform: uppercase;
    background-color: rgb(43, 149, 219);
    background-image: linear-gradient(125deg, rgb(43, 149, 219) 0%, rgb(130, 71, 176) 50%, rgb(43, 149, 219) 100%);
    color: rgb(255, 255, 255);
    border-radius: 1rem;
    padding: 1rem 2rem;
    width: 100%;
    text-align: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    transition: all 200ms ease 0s;
    font-size: 1.3rem;
    border: none;
}
.button-div button:hover{
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    box-shadow: 0px 10px 20px -9px rgba(0,0,0,0.75);
}
.button-div{
    display: flex;
    height: 100%;
    justify-content: center;
    align-items: center;
}
.layout_parent{
    margin: 0 auto;
display: grid;
width: 70%;
grid-template-columns: 1fr 1fr;
grid-template-rows: 1fr 1fr;

  grid-gap: 1em;

  gap: 1em;
}
.stats-div_1{
    display: flex;
    align-items: center;
    justify-content: center;
}
.stats-div_1 p{
    color: rgb(43, 149, 219);
    font-size: 4rem;
    text-shadow: rgb(24 25 28) 0px 0px 10px;
    margin: 0
}
.stats-div_1 span{
    color: rgb(43, 149, 219);
    font-size: 2rem;
}
.daily-return{
    color: rgb(43, 149, 219);
}
.apr_container, .fee_container{
    line-height: 1.3rem;
    padding: 0.1rem 0.4rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.apr_container{
    margin-top: 1em
}
.input-container{
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid rgb(43, 149, 219);
    border-radius: 1rem;
    padding: 0.5rem 2rem;
    background-color: rgb(255, 255, 255);
    width: 100%;
}

.input-container input{
    flex-grow: 1;
    flex-shrink: 1;
    font-size: 1.3rem;
    margin: 0.5rem;
    width: 10ch;
    color: rgb(24, 25, 28);
    
    border: none
}
.button{
    text-transform: uppercase;
    background-color: rgb(34, 118, 173);
    background-image: unset;
    color: rgb(255, 255, 255);
    border-radius: 1rem;
    padding: .5rem 1rem;
    width: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    transition: all 200ms ease 0s;
    font-size: 1em;
    border: none
}
h1{
    color: rgb(255, 255, 255);
    font-size: 1.5em;
    text-transform: uppercase;
    text-shadow: rgb(0 233 206) 0px 0px 4px;
    text-align: left;
    margin: 0 
}
.parrafo{
    font-size: 0.8rem;
    text-align: left;
    margin: 0;
}
.connect_div{
    grid-row: span 2;
}

@media (max-width: 840px){
    .layout_parent{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    }
    .connect_div, .stats_div, .refferral_div{
        width: 100%
    }
}
.refferral_div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}
.span-cro{
    font-size: .7rem;
    font-weight: 600;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}
.absolve-div{
    display: flex;
    justify-content: center;
    align-items: center;
}
.absolve-div button{
    text-transform: uppercase;
    background-color: rgb(43, 149, 219);
    background-image: linear-gradient(125deg, rgb(43, 149, 219) 0%, rgb(130, 71, 176) 50%, rgb(43, 149, 219) 100%);
    color: rgb(240, 240, 240);
    border-radius: 1rem;
    padding: 1rem 2rem;
    width: 100%;
    text-align: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    transition: all 200ms ease 0s;
    font-size: 1.3rem;
    border: none;
}
.absolve-div button:hover{
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    box-shadow: 0px 10px 20px -9px rgba(0,0,0,0.75);
}
.cro-p{
    font-size: 1.4rem;
    color: #000;
    margin: 0;

}
.hr{
    width:100%;
    height: 4px;
    border: none;
    background: rgb(43, 149, 219);;
}
.reward-container{
    display: flex;
    
    justify-content: space-between;
    display: flex;
    -webkit-box-pack: center;
    align-items: center;
    border: 1px solid rgb(43, 149, 219);
    border-radius: 1rem;
    padding: 0 2rem;
    background-color: rgba(0,0,0,0.5);
    width: 100%;
    box-shadow: inset 0px 0px 12px 0px rgba(43, 149, 219);
}

.reward-container p.reward {
    margin-top: 15px;
}
.amount-cro{
    display: flex;
    align-items: center;
}
.amount-cro p{
    font-size: 1.6rem;
    font-weight: 600;
}

.amount-cro p.reward-amount {
    margin-top: 15px;
}

.span-cro{
    margin-top: 25px;
    font-size: .8rem;
}
.button_parent{
    display: flex;
    align-items: center;
    justify-content: space-between;
    grid-gap: 1rem;
    gap: 1rem;
}
.button-absolve{
    text-transform: uppercase;
    background-color: rgb(84, 135, 211);
   
    color: rgb(240, 240, 240);
    border-radius: 1rem;
    padding: 0 1rem;
    width: 100%;
    align-items: center;
    display: flex;
    flex-direction: column;
    transition: all 200ms ease 0s;
    font-size: 1em;
    border: none
}
.button-absolve p{
    margin-top: 10px;
    margin-bottom: 0;
}

.button-absolve span{
    margin-bottom: 20px;
    font-size: .8rem;
}
.button-absolve:hover{
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    box-shadow: 0px 10px 20px -9px rgba(0,0,0,0.75);
}
.button-container{
    
    flex-grow: 1;
}

.icon_container{
    margin-top: 3em
}

.icon-footer{
    height: 100px;
}

.icon_parent{
    display: flex;
    grid-gap: 1em;
    gap: 1em;
    width: 100%;
    margin: 0 auto;
    justify-content: center;
}

.developed_by{
    padding-top: 2rem;
    font-size: 0.8rem;
    color: rgb(240, 240, 240);
    bottom: 0px;
}

.countdown {
    margin-top: 30px;
}

#countdown_dashboard {
    margin-top: 15px;
    height: 50px;
    width: 100%;
    display: flex;
    justify-content: center;
}

.dash {
    width: 80px;
    height: 80px;
    margin-left: 10px
}

.dash .digit {
    font-size: 30pt;
    font-weight: 700;
    font-family: Arial, sans-serif!important;
    width: 39px;
    margin: 0 1px 0 0 ;
    color: #fff;
    border-radius: 4px;
    text-shadow: 1px 1px 1px #0A0A0A;
}

.dash_title {
    bottom: 0;
    left: 0;
    font-size: 9pt;
    color: #555;
    letter-spacing: 2px
}


.dash,
.dash .digit {
    position: relative;
    float: left
}


.dash_title {
    position: absolute;
    display: block;
    text-transform: uppercase
}

.dash .digit {
  padding: 10px 0;
    text-align: center;
