.countdown {
    margin-top: 30px;
}

#countdown_dashboard {
    margin-top: 15px;
    height: 50px;
    width: 100%;
    display: flex;
    justify-content: center;
}

.dash {
    width: 80px;
    height: 80px;
    margin-left: 10px
}

.dash .digit {
    font-size: 30pt;
    font-weight: 700;
    font-family: Arial, sans-serif!important;
    width: 39px;
    margin: 0 1px 0 0 ;
    color: #fff;
    -webkit-border-radius: 4px;
    border-radius: 4px;
    text-shadow: 1px 1px 1px #0A0A0A;
}

.dash_title {
    bottom: 0;
    left: 0;
    font-size: 9pt;
    color: #555;
    letter-spacing: 2px
}


.dash,
.dash .digit {
    position: relative;
    float: left
}


.dash_title {
    position: absolute;
    display: block;
    text-transform: uppercase
}

.dash .digit {
  padding: 10px 0;
    text-align: center;
/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#636363+0,333333+52,414242+53,161616+100 */
